<script>
import database from '@/services/database';

    export default {
      name: 'signin',
      data() {
          return {
              email: '',
              password: '',
              error: ''
          }
      },
      methods:  {
          async signIn () {
              let result = await database.signIn(this.email, this.password);
              
              if (result.message) {
                  this.error = result.message;
              } else {
                  console.log('User is signed in.');
                  this.$router.push('/profile');
              }
          }
      }
    };
</script>